body {
      background-color: rgb(255, 255, 255);
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  .map-books {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 80%;
    margin: 3em auto 0px;
    color: black;
    font-size: 0.8em;
    margin-top: -0.5rem;
  }
  .card  {
    width:  140px;
    height: 190px;
    text-align: center;
    margin: 1em 1em 7.2em 1em;
  }
  
  .card img {
    width: 140px;
    height: 190px;
    border-radius: 1em;
  }
  .homepage {
    /* background-color: rgb(0, 0, 0); */
    background-color: rgb(247, 247, 247);
    padding: 10px;
    text-align: center;
    top: 0px;
    height: 2rem;
    
  }
  
  .search {
    width: 20%;
    top: -10.6em;
    position: relative;
    display: flex;
    margin-left: 65.5%;
    height: 2em;
    
  }
  
  .span-name {
    font-size: 1.2em;
  }
  .ibdb{
    font-size: 2.6rem;
    position: relative;
    top: -2.7rem;
    font-family: 'Anton', sans-serif;
    text-shadow: 4.1px 2px #cdcecd;
    /* color: rgb(214, 214, 205); */
    color: rgb(159, 240, 199);
    text-shadow: 4.1px 2px #4b4a43;
  }
  .imgDescription {
    margin: 1em 2px auto ;
    border-radius: 1em;
    width:  150px;
    height: 200px;
    border: 1px solid rgb(216, 214, 214);
  }
  
  .ibdb-description {
    background-color: rgb(247, 247, 247);
    padding: 10px;
    text-align: center;
    top: 0px;
    height: 2rem;
    position: relative;
    
  }

  .rating {
    color: rgb(138, 125, 125);
    text-align: center;
    
  }

  #amb {
    position: relative;
    top: -2.1rem;
    color: rgb(145, 40, 40);
  }
  
  .imgDescription-container {
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   margin: 0px 20%;
   justify-content: center;
   top: 2em;
   position: relative;
  }
  .imgDescription-container img{
    margin: auto;
    
  }
  .descriptin-flex {
    width: 35em;
    margin: auto;
    text-align: left;
    line-height: 1.6;
    font-size: 1.11em;
  
  }
  @media only screen and (max-width: 1050px) {
    .imgDescription-container {  
      margin: auto;
      width: 80%;
    
     }
  }
  
  .ibdb-logo {
    font-size: 2.6rem;
    position: relative;
    top: -2.7rem;
    font-family: 'Anton', sans-serif;
    text-shadow: 4.1px 2px #cdcecd;
    color: rgb(159, 240, 199);
    text-shadow: 4.1px 2px #4b4a43;
  
  }
  
  .select {
   margin-top: -12.75em;
   margin-right: 37em;
   display: none;
  }
  
  
  .select-bar {
    background-color: rgb(214, 214, 205);
    color: black;
    margin: 5px auto;
    border: 1px solid silver;
    display: flex;
    position: relative;
    top: -0.8em; 
  }
  
  .name-of-author {
    text-decoration: underline ;
    text-align: center;
  }
  
  .imgBy {
    display: flex;
    border-radius: 50%;
    width:  100px;
    height: 100px;
    border: 1px solid silver;
  }
  .name-and-by {
    position: relative;
    top: 0.5em;
  }
  
  .qu{
     background-color: rgb(80, 73, 66) ; 
     height: 8em;
 
  }
  
  .quotebox {
    font-size: 0.7em;
    background-color: transparent ; 
    margin-top: -7rem;
    height: 12em;
    /* color: rgb(189, 202, 204); */
    color: rgb(121, 124, 124);
   
    
  }

  .newquote {
    /* background-color: rgb(244, 244, 248);
    color: rgb(47, 49, 66);
    border: 1px solid silver;
    font-size: 1.2em; */
    display: none;
  }
  
  .new {
    border: 1px solid silver;
  }
  
  .description-body {
    margin:  auto ;
    justify-content: center;
    position: relative;
    top: -4.2rem;
    background-color: rgb(255, 255, 255);
  }
  
  .scroll {
    position: relative;
    top: 10em;
  }
  
  .scroll-description{
    position: relative;
    top: -3em;
    height: 100em;
   
  }
  
  .sidebar {
    position: fixed;
    transition: all 0.5s;
    transform: translateX(-30rem);
    width: 380px;
    background-color: #dadada;
    height: 16.5rem;
    display: block;
    top: 3.25rem;
    margin-right: -19.2em;
    z-index: 10;

  }
  
  .sidebar ul {
    list-style-type: none;
    
  }
  
  .sidebar.open {
    transform: translateX(-80px);
    
  }
  
  .sidebar-close-button{
    position: relative;
    top: -2.45rem;
    margin-right: 11.8rem;
    width: 2rem;
    height: 1.7rem;
    justify-content: center;
    align-items: center;
    cursor: pointer;

  }  
  
  .sidebar-open-button {
    position: relative;
    top: -7.1rem;
    margin-right: 60rem;
    display: flex;
    cursor: pointer;
    width: 2rem;
    height: 1.7rem;
    justify-content: center;
    align-items: center;
  }

  #amb {
    color: rgb(236, 120, 42);
  }
  
  
